<template>
    <div>
        <Row>
            <Button type="primary" @click="add" v-permission.show="'b:perm:platform:create'">
                <Icon type="plus"></Icon>开通平台商
            </Button>
        </Row>
        <Row class="margin-top-10">
            <label style="margin-right:8px;">平台：</label>
            <Select v-model="table.query.platform" clearable style="width: 200px">
                <Option v-for="item in platformTypes" :value="item.id" :key="item.id">{{ item.name }}</Option>
            </Select>
            <label class="margin-left-10" style="margin-right:8px;">机构代码：</label>
            <Input v-model="table.query.code" placeholder="" clearable style="width: 200px" />
            <label class="margin-left-10" style="margin-right:8px;">企业名称：</label>
            <Input v-model="table.query.name" placeholder="" clearable style="width: 200px" />
            <Button class="margin-left-10" type="primary" @click="doQuery" icon="md-search">搜索</Button>
        </Row>
        <Row class="margin-top-10">
            <i-col>
                <Table :columns="table.col" :data="table.data"></Table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="table.total" :show-sizer="true" placement="top" @on-page-size-change="changePageSize" @on-change="changePage"></Page>
                    </div>
                </div>
            </i-col>
        </Row>
        <Modal v-model="model" :mask-closable="false" :title="modelTitle" :loading="loadingBtn" @on-ok="addOrUpdate" @on-cancel="cancel">
            <div style="height: 300px">
                <vue-scroll ref="vScroll">
                    <Form ref="form" :model="vo" :label-width="80" :rules="validate">
                        <Form-item label="机构代码" prop="code" v-show="!isUpdate">
                            <SearchSelect ref="codeSelect" v-model="vo.code" :on-search="searchEpByCode" @blur="searchEpByCode" select-width="auto" @change="changeEp"/>
                        </Form-item>
                        <Form-item label="Logo" prop="logoPic">
                            <OssUpload :files="logoPic" :size="1" :format="['jpg','jpeg','png']" accept="image/*" :disabled="hasOld" />
                        </Form-item>
                        <Form-item label="企业名称" prop="name">
                            <Input v-model.trim="vo.name" :disabled="hasOld" />
                        </Form-item>
                        <Form-item label="法人" prop="legalPerson">
                            <Input v-model.trim="vo.legalPerson" :disabled="hasOld" />
                        </Form-item>
                        <Form-item label="营业执照" prop="licensePic">
                            <OssUpload :files="licensePic" :size="1" :format="['jpg','jpeg','png']" accept="image/*" :disabled="hasOld" />
                        </Form-item>
                        <Form-item label="有效期" prop="licensePeriod">
                            <Input v-model.trim="vo.licensePeriod" :disabled="hasOld" />
                        </Form-item>
                        <Form-item label="注册资本" prop="registerCapital">
                            <Input v-model.trim="vo.registerCapital" :disabled="hasOld" />
                        </Form-item>
                        <Form-item label="注册日期" prop="registerDate">
                            <DatePicker v-model="registerDate" :disabled="hasOld" type="date" transfer placeholder="请选择注册日期" style="width: 150px"></DatePicker>
                        </Form-item>
                        <Form-item label="经营范围" prop="businessScope">
                            <Input v-model.trim="vo.businessScope" :disabled="hasOld" />
                        </Form-item>
                        <Form-item label="注册地址" prop="address">
                            <al-cascader ref="address" v-model="address" :level="2" :disabled="hasOld" />
                            <Input v-model="vo.streetAddress" :disabled="hasOld" placeholder="请输入街道地址" />
                        </Form-item>
                        <Form-item label="办公地址" prop="officeAddress">
                            <al-cascader ref="officeAddress" v-model="officeAddress"  :level="2" :disabled="hasOld" />
                            <Input v-model="vo.officeStreetAddress" :disabled="hasOld" placeholder="请输入街道地址" />
                        </Form-item>
                        <FormItem label="状态" prop="status" v-show="isUpdate">
                            <Select transfer v-model="vo.status">
                                <Option v-for="item in EpStatus" :value="item.id" :key="item.id">{{ item.name }}</Option>
                            </Select>
                        </FormItem>
                        <template v-if="!isUpdate">
                            <Divider>管理员信息</Divider>
                            <Form-item label="用户名" prop="user.username">
                                <Input v-model.trim="vo.user.username" :disabled="hasOld" />
                            </Form-item>
                            <Form-item label="昵称" prop="user.nickname">
                                <Input v-model.trim="vo.user.nickname" :disabled="hasOld" />
                            </Form-item>
                            <FormItem label="用户角色" prop="roles">
                                <Select transfer v-model="vo.roles" filterable multiple>
                                    <Option v-for="item in roles" :value="item.id" :key="item.id">{{ item.name }}</Option>
                                </Select>
                            </FormItem>
                            <Divider>平台信息</Divider>
                            <Form-item label="平台名称" prop="platform.name">
                                <Input v-model.trim="vo.platform.name" />
                            </Form-item>
                            <Form-item label="平台标识" prop="platform.key">
                                <Input v-model.trim="vo.platform.key" />
                            </Form-item>
                        </template>
                    </Form>
                </vue-scroll>
            </div>
        </Modal>
    </div>
</template>
<script>
import dayjs from 'dayjs';
import SearchSelect from '@/components/search-select';
import OssUpload from '@/components/upload';
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import ModelView from '@/mixins/model-view';
import { EpStatus } from '@/libs/dic';

export default {
    name: 'PlatformIndex',
    components: { SearchSelect, OssUpload },
    mixins: [ TableDataView, TableColRender, ModelView ],
    data () {
        return {
            EpStatus,
            oldEp: null,
            table: {
                col: [{
                    title: '机构代码',
                    key: 'code'
                }, {
                    title: '企业名称',
                    key: 'name'
                }, {
                    title: '法人',
                    key: 'legalPerson'
                }, {
                    title: '注册资本',
                    key: 'registerCapital',
                    tooltip: true
                }, {
                    title: '平台',
                    key: 'platformNames',
                    render: (h, params) => this.renderListPop(h, params, val => val ? String(val).split(',') : [])
                }, {
                    title: '状态',
                    key: 'status',
                    render: (h, params) => this.renderDic(h, params)(EpStatus)
                }, {
                    title: '创建时间',
                    key: 'createTime',
                    render: this.renderDate
                }, {
                    title: '更新时间',
                    key: 'updateTime',
                    render: this.renderDate
                }, {
                    title: '操作',
                    key: 'action',
                    width: 200,
                    align: 'center',
                    render: (h, params) => this.renderActions(h, params, [{
                        config: { loading: this.loadingBtn, style: { marginRight: '5px' }, disabled: params.row.type === 0 },
                        click: () => this.update(params.row),
                        text: '修改'
                    }])
                }],
                url: '/api/platform/list',
                query: {
                    platform: null,
                    code: null,
                    name: null
                }
            },
            platformTypes: [],
            roles: [],
            address: [],
            officeAddress: [],
            registerDate: null,
            licensePic: [],
            logoPic: [],
            vo: {
                code: null,
                name: null,
                legalPerson: null,
                licensePeriod: null,
                registerCapital: null,
                registerDate: null,
                businessScope: null,
                addressCode: null,
                address: null,
                streetAddress: null,
                officeAddressCode: null,
                officeAddress: null,
                officeStreetAddress: null,
                licensePic: null,
                logoPic: null,
                status: 1,
                user: {
                    username: null,
                    phone: null,
                    nickname: null
                },
                roles: [],
                platform: {
                    name: null,
                    key: null
                },
                epType: 1
            },
            validate: {
                code: [{ required: true, trigger: 'blur', message: '请填写企业机构代码' }],
                name: [{ required: true, trigger: 'blur', message: '请填写企业名称' }],
                'user.username': [{ required: true, trigger: 'blur', message: '管理员用户名不能为空' }],
                roles: [{ required: true, type: 'array', trigger: 'change', message: '请选择管理员角色' }],
                'platform.name': [{ required: true, trigger: 'blur', message: '请填写平台名称' }],
                'platform.key': [{ required: true, trigger: 'blur', message: '请填写平台唯一标识' }]
            },
            addUrl: '/api/platform/create',
            updateUrl: '/api/fx/ep/update'
        };
    },
    computed: {
        hasOld () {
            return !!this.oldEp;
        },
        generateAddOrUpdate () {
            const last = this.address[this.address.length - 1] || {};
            const office = this.officeAddress[this.officeAddress.length - 1] || {};
            return { ...this.vo,
                addressCode: last.code,
                address: this.address.map(item => item.name).join(''),
                officeAddressCode: office.code,
                officeAddress: this.officeAddress.map(item => item.name).join(''),
                registerDate: this.registerDate && dayjs(this.registerDate).format('YYYY-MM-DD'),
                licensePic: this.licensePic?.length ? this.licensePic[0].name : null,
                logoPic: this.logoPic?.length ? this.logoPic[0].name : null
            };
        }
    },
    watch: {
        'vo.user.username' (val) {
            this.vo.user.phone = val;
        }
    },
    async created () {
        await this.loadPlatformTypes();
    },
    methods: {
        async add () {
            await this.loadAllRoles();
            this.$super(ModelView).add();
            this.oldEp = null;
            this.registerDate = null;
            this.licensePic = [];
            this.logoPic = [];
            this.$refs.codeSelect.clear();
            this.vo.streetAddress = null;
            this.vo.addressCode = null;
            this.vo.officeStreetAddress = null;
            this.vo.officeAddressCode = null;
            this.$nextTick(() => {
                this.$refs.address.$children[0].clearSelect();
                this.$refs.officeAddress.$children[0].clearSelect();
            });
        },
        async update (item) {
            await this.loadAllRoles();
            this.oldEp = null;
            this.$super(ModelView).update(item);
            this.setByItem(item);
        },
        setByItem (item) {
            if (item.addressCode) {
                this.setAddress('address', item.addressCode);
            }
            if (item.officeAddressCode) {
                this.setAddress('officeAddress', item.officeAddressCode);
            }
            if (item.registerDate) {
                this.registerDate = dayjs(item.registerDate).toDate();
            }
            if (item.licensePic) {
                this.licensePic = [{ name: item.licensePic, status: true }];
            }
            if (item.logoPic) {
                this.logoPic = [{ name: item.logoPic, status: true }];
            }
        },
        setAddress (name, val) {
            this[name] = [
                val.substring(0, 2).padEnd(6, '0'),
                val.substring(0, 4).padEnd(6, '0'),
                val
            ];
        },
        changeEp (item) {
            this.oldEp = item;
            ['code', 'name', 'legalPerson', 'licensePeriod', 'registerCapital', 'registerDate', 'businessScope']
                .filter(k => Reflect.has(item, k)).forEach(k => this.vo[k] = item[k]);
            this.setByItem(item);
            this.vo.user.username = item.admin.username;
            this.vo.user.nickname = item.admin.nickname;
        },
        async searchEpByCode () {
            if (!this.vo.code) {
                this.oldEp = null;
                return [];
            }
            const result = await this.get(`/api/ep/code/${this.vo.code}`, { admin: true, platform: true });
            if (result.success && result.value) {
                this.changeEp(result.value);
            } else {
                this.oldEp = null;
            }
            return [];
        },
        async loadPlatformTypes () {
            const result = await this.get('/api/platform/types');
            this.platformTypes = result.success ? result.value : [];
        },
        async loadAllRoles () {
            const result = await this.get('/api/permission/role/list', { epType: 1, type: 1 });
            if (result.success) {
                this.roles = result.value;
            }
        }
    }
};
</script>
